<template>
	<div class="conteiner-fluid">
		<h3 class="withWave"><span class="star">Популярные товары</span></h3>
		<div class="background">
			<div class="content">
				<carousel :perPageCustom="[[201, 1], [701, 2], [1020, 2], [1021, 3], [1380, 4]]" :loop="true">
					<slide
							v-for="(image, imageIndex) in images"
							:key="imageIndex"
							:class="image">
						<div class="productWrap">
							<div class="product-photo">
								<img :src="image.url" :alt="'gg' + image.price">
							</div>
							<div class="product-info">
								<div class="title d-flex align-content-center flex-wrap">
									<h2>{{image.title}}</h2>
								</div>
								<div class="price-block">
									<div class="row">
										<div class="col-6">
											<div class="price">
												{{image.price + 'грн'}}
											</div>
										</div>
										<div class="col-6">
											<div class="button">Купить</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</slide>
				</carousel>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	props: ['url'],
  data: function () {
    return {
      images: [
        {
					url: require('../../../assets/images/photo/popular/good_1.jpg'),
					title: 'Автокресло детское Bambi M 2782-2',
					price: '2000'
				},
        {
					url: require('../../../assets/images/photo/popular/good_2.jpg'),
					title: 'Джип Porsche Cayenne YJ288 R/C',
					price: '4590'
				},
				{
					url: require('../../../assets/images/photo/popular/good_3.jpg'),
					title: 'Детские качели M 2130-4',
					price: '2049'
				},
				{
					url: require('../../../assets/images/photo/popular/good_4.jpg'),
					title: 'TILLY Cayman T-381',
					price: '2120'
				}
      ],
      index: null
    }
  },
	mounted () {
	}
}
</script>



