<template>
	<section class="product-page">
		<div class="breadCrumbs">
			<div class="content"><i class="fas fa-home"></i> главная / детский транспорт / {{ windowWidth + ' / ' + showOptions }}</div>
		</div>
		<div class="content">
			<div class="container-fluid">
				<div class="row">
					<transition name="slide-up-fade">
						<Sidebar class="d-none d-lg-block" :dropmenu="dropmenu" v-if="windowWidth >= 974"/>
						<Sidebar class="optionsList" 
							:dropmenu="dropmenu" 
							v-else-if="showOptions"
							:class="{standartHeight: scrollPosition < 100, changedHeight: scrollPosition > 100}"
							/>
					</transition>
					<Products class="col"/>
				</div>
				<div class="filters d-block d-lg-none" :class="{ 'bottom_line': showOptions }">
					<button class="button shadow-sm" @click="showOptions = !showOptions"><span>фильтры</span><i class="fas" :class="{'fa-sliders-h': !showOptions, 'fas fa-angle-left': showOptions}"></i></button>
				</div>
			</div>
		</div>
	</section>
</template>
<script>
import Sidebar from '../components/category_page/Sidebar'
import Products from '../components/category_page/Products'
	export default {
		components: {
			Sidebar,
			Products
		},
		data () {
			return {
				showOptions: false,
				scrollPosition: null,
				windowWidth: 0,
				dropmenu: [ 
					{
						link: '/child-car',
						title: 'Детский транспорт',
						class: 'nav-item'
					},{
						link: '/',
						title: 'Детская мебель',
						class: 'nav-item'
					},{
						link: '/how-it-work',
						title: 'Игровые наборы',
						class: 'nav-item'
					},{
						link: '/how-it-work',
						title: 'Игрушки',
						class: 'nav-item'
					},{
						link: '/creator',
						title: 'Коляски',
						class: 'nav-item'
					},{
						link: '/how-it-work',
						title: 'Надувные изделия',
						class: 'nav-item'
					}
				]
			}
		},
		mounted() {
			this.$nextTick(function() {
				window.addEventListener('resize', this.getWindowWidth);	
				window.addEventListener('scroll', this.updateScroll);

				this.getWindowWidth()
				this.updateScroll()
			})

		},
		methods: {
			getWindowWidth(event) {
				this.windowWidth = document.documentElement.clientWidth;
			},
			updateScroll: function() {
				this.scrollPosition = window.scrollY
			}
		},
		beforeDestroy() {
			window.removeEventListener('resize', this.getWindowWidth);
			window.removeEventListener('scroll', this.updateScroll);
		}
	}
</script>



