<template>
	<section class="sliderWrap">
		<div class="content">
			<div class="container-fluid">
				<div class="row">
					<div class="col">
						<carousel :per-page="1" :loop="true">
							<slide 
									v-for="(image, imageIndex) in gallery" 
									:key="imageIndex">
									<img :src="image.url" :alt="image.alt">
							</slide>
						</carousel>
					</div>
					<div class="col-lg-auto col-12 new_goods">
						<div class="row">
							<div class="col-12 col-sm-6 col-md-6 col-lg-12 good_wrapper">
								<img :src="require('../../../assets/images/photo/good_4.jpg')" alt="ggf">
								<div class="title">
									<h2>Новые мягкие игрушки</h2>
								</div>
							</div>
							<div class="col-12 col-sm-6 col-md-6 col-lg-12 good_wrapper">
								<img :src="require('../../../assets/images/photo/good_3.jpg')" alt="ggf">
								<div class="title">
									<h2>Новые велосипеды</h2>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
  </section>
</template>
<script>
	export default {
		data () {
			return {
				gallery: [
					{
						alt: 'bag',
						url: require('../../../assets/images/photo/good_1.jpg')
					},
					{
						alt: 'walk',
						url: require('../../../assets/images/photo/good_2.jpg')
					},
					{
						alt: 'bicikle',
						url: require('../../../assets/images/photo/good_3.jpg')
					}
				]
			}
		}
	}
	
</script>



