<template>
	<section id="new" class="conteiner-fluid new">			
		<div class="content">
			<h2 class="withWave">Не пропустите новинки в разделах</h2>
			<div class="container-fluid">
				<div class="row">
					<div class="col-6 col-sm-4 col-lg-2" 
						v-for="(sale, saleIndex) in saleWrap"
						:key="saleIndex"
					>
						<div class="goodWrap">
							<div class="images" @click="!indexClick" :class="{'active': !indexClick}">
								<img :src="sale.url" :alt="sale.alt">
							</div>
							<div class="title">
								{{ sale.name }}
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</section>
</template>

<script>
	export default {
		data () {
			return {
				indexClick: 1,
				saleWrap: [
					{
						name: 'Автокресла',
						url: require('../../../assets/images/photo/sale/good_1.jpg'),
						alt:''
					},
					{
						name: 'Лошадки',
						url: require('../../../assets/images/photo/sale/good_2.jpg'),
						alt:''
					},
					{
						name: 'Коляски',
						url: require('../../../assets/images/photo/sale/good_3.jpg'),
						alt:'col'
					},
					{
						name: 'Столики',
						url: require('../../../assets/images/photo/sale/good_4.jpg'),
						alt:'table'
					},
					{
						name: 'Синтезаторы',
						url: require('../../../assets/images/photo/sale/good_5.jpg'),
						alt:'sint'
					},
					{
						name: 'Электромобили',
						url: require('../../../assets/images/photo/sale/good_6.jpg'),
						alt:'electro'
					}
				]
			}
		}
	}
</script>



