import Inst from '../../assets/images/svg/instagram.svg'
import Fb from '../../assets/images/svg/facebook.svg'
import Tg from '../../assets/images/svg/tg.svg'
import Cart from '../../assets/images/svg/cart.svg'
export default {
	components: {
		CartBlock,
		Logo,
		Inst,
		Fb,
		Tg,
		Cart
	},
	data: function () {
		return {
			cartShow: true,
			visible: false,
			menuOn: true,
			scrollPosition: null,
			count: 1,
			menu: [
				{
					link: '/',
					title: 'Статьи и новости',
					class: 'nav-item'
				},{
					link: '/how-it-work',
					title: 'Условия работы',
					class: 'nav-item'
				},
				{
