					link: '/',
					title: 'Главная',
					class: 'nav-item'
				},{
					link: '/category-page',
					title: 'Детский транспорт',
					class: 'nav-item'
				},{
					link: '/',
					title: 'Детская мебель',
					class: 'nav-item'
				},{
					link: '/how-it-work',
					title: 'Игровые наборы',
					class: 'nav-item'
				},{
					link: '/how-it-work',
					title: 'Игрушки',
					class: 'nav-item'
				},{
