					title: 'Статьи и новости',
					class: 'nav-item d-block d-lg-none'
				},{
					link: '/how-it-work',
					title: 'Условия работы',
					class: 'nav-item d-block d-lg-none'
				},
				{
					link: '/how-it-work',
					title: 'Оптовым покупятелям',
					class: 'nav-item d-block d-lg-none'
				},
				{
					link: '/aboute-comp',
					title: 'О компании',
					class: 'nav-item d-block d-lg-none'
				},
				{
					link: '/how-it-work',
					title: 'Акции и скидки',
					class: 'nav-item d-block d-lg-none'
				}
			]
    }
	},
	computed: {
		myCount () {
			return {
				count: this.count
			}
		}
	},
  methods: {
    updateScroll: function() {
      this.scrollPosition = window.scrollY
		},
		increase () {
			this.count += 1
		},
		decrease () {
			if ( this.count <= 0) {
				this.count = 1
			}
			else {
				this.count -= 1
			}
		}
  },
  mounted() {
    window.addEventListener('scroll', this.updateScroll);
  }
}
</script>



