<template>
	<section class="product">
		<div class="breadCrumbs">
			<div class="content"><i class="fas fa-home"></i> главная / детский транспорт / {{ windowWidth + ' / ' + showOptions }}</div>
		</div>
		<div class="content">
			<div class="container-fluid">
				<div class="row">
					<div class="col-lg-auto col-12">
						<div class="product-gallery">
							<div class="gallery">
								<div class="active-image">
									<img :src="imageActive" :alt="'product-' + productId">
								</div>
								<div class="image-row">
									<div class="container-fluid">
										<div class="row">
											<div class="col-auto"
												v-for="(product, productId) in productList"
												:key="productId"
												@click="imageActive = product.url"
												:class="{'active': imageActive == product.url}"
											>
												<img :src="product.url" :alt="'product-' + productId">
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
					<div class="col-lg col-md-12">
						<div class="product-description-block">
							<div class="title">
								<h1>Автокресло детское</h1>
								<p>Bambi M 2782-2</p>
							</div>
							<div class="rating">
								<div class="stars">

								</div>
							</div>
							<div class="description">
								<p>
									Lorem ipsum dolor sit amet consectetur adipisicing elit. Suscipit consequuntur doloribus assumenda odio similique exercitationem commodi quia asperiores totam facere delectus, harum molestias laborum dignissimos dicta quo corrupti quaerat perspiciatis?
								</p>
							</div>
							<div class="soc-links">
								<a href="#"><Tg/></a><a href="#"><Fb/></a><a href="#"><Inst/></a>
							</div>
							<div class="buy-block">
								<div class="container-fluid">
									<div class="row">
										<div class="price" v-if="promo === '2323'"><strike>4590грн</strike> 450грн</div>
										<div class="price" v-else>4590грн</div>
										<div class="button">купить</div>
									</div>
								</div>
							</div>
							<div class="for-optCust">
								<h3>Введите промокод (2323), чтоб увидеть оптовую цену:</h3>
								<div class="form">
									<form class="form-inline">
										<input type="text" class="form-control" id="inputPassword2" v-model="query">
										<div class="btn btn-primary mb-2"
											@click="promo = query"
										>узнать цену</div>
									</form>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="container-fluid comments-block">
			<div class="content">
				<h2>Отзывы о товаре:</h2>
				<div class="container-fluid">
					<div class="row">
						<div class="col-12 col-md-6 comments-wrap">
							<div class="comment">
								<div class="text">
									<h3>Заголовок отзыва</h3>
									<p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Vero sequi quasi, rem voluptatum temporibus sit nihil, ab reprehenderit, eius placeat cumque quidem. Nemo ea eius molestiae totam porro, tempore laudantium?</p>
								</div>
								<div class="author-info">
									<span class="name">Имя Фамилия</span><span class="date">13.05.18</span>
								</div>
							</div>
						</div>
						<div class="col-12 col-md-6 add-comments">
							<form>
								<h3 for="exampleFormControlTextarea"><span class="star">Ваши данные</span></h3>
								<div class="row">
									<div class="col">
										<input type="text" class="form-control" placeholder="Как Вас зовут?">
									</div>
									<div class="col">
										<input type="text" class="form-control" placeholder="Какая у Вас фамилия?">
									</div>
								</div>
								<input type="email" class="form-control" id="inputEmail3" placeholder="Введите Ваш e-mail">
								<div class="form-group">
									<h3 for="exampleFormControlTextarea">Текст комментария</h3>
									<input type="text" class="form-control" id="inputEmail5" placeholder="Придумайте заголовок">
									<textarea class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
								</div>
								<div class="btn btn-primary mb-2">Оставить отзыв</div>
							</form>
						</div>
					</div>
				</div>
			</div>
		</div>
	</section>
</template>

<script>
import Inst from '../../assets/images/svg/instagram.svg'
import Fb from '../../assets/images/svg/facebook.svg'
import Tg from '../../assets/images/svg/tg.svg'

export default {
	components: {
		Inst,
		Fb,
		Tg
	},
	data () {
		return {
			newPrice: '',
			promo: '',
			query: '',
			windowWidth: 0,
			imageActive: require('../../assets/images/photo/popular/good_1.jpg'),
			productList: [
      	{
					url: require('../../assets/images/photo/popular/good_1.jpg'),
					title: 'Автокресло детское Bambi M 2782-2',
					price: '2000'
				},
      	{
					url: require('../../assets/images/photo/popular/good_2.jpg'),
					title: 'Джип Porsche Cayenne YJ288 R/C',
					price: '4590'
				},
				{
					url: require('../../assets/images/photo/popular/good_3.jpg'),
					title: 'Детские качели M 2130-4',
					price: '2049'
				},
				{
					url: require('../../assets/images/photo/popular/good_4.jpg'),
					title: 'TILLY Cayman T-381',
					price: '2120'
				}
			]
		}
	},
	mounted() {
		this.$nextTick(function() {
			window.addEventListener('resize', this.getWindowWidth);	
			window.addEventListener('scroll', this.updateScroll);
			this.getWindowWidth()
			this.updateScroll()
		})
	},
	methods: {
		getWindowWidth(event) {
			this.windowWidth = document.documentElement.clientWidth;
		},
		updateScroll: function() {
			this.scrollPosition = window.scrollY
		}
	},
	beforeDestroy() {
		window.removeEventListener('resize', this.getWindowWidth);
		window.removeEventListener('scroll', this.updateScroll);
	}
}
</script>




