<template>
	<div class="cart-wrap" v-if="!cartShow">
		<div class="cart">
			<div class="checkout">
				<div class="cart_header">
					<h3>Корзина</h3>
					<div class="close" @click="cartShow = !cartShow">
						<i class="fa fa-times " style="font-size:26px"></i>
					</div>
				</div>
				<div class="cart_content">
					<div class="container-fluid">
						<div class="row first-row">
							<div class="col-auto">
								<div class="image">
									<img :src="require('../../assets/images/photo/popular/good_1.jpg')" alt="good">
								</div>
							</div>
							<div class="col">
								<div class="name">
									<span>Автокресло</span>
								</div>
							</div>
							<div class="col">
								<div class="count">
									<div class="count-row">
										<button @click="increase ()"><i class="fas fa-plus "></i></button>
										<input v-model.number="count" type="text">
										<button @click="decrease ()"><i class="fas fa-minus "></i></button>
									</div>
								</div>
							</div>
							<div class="price-block">
								<div class="price">
									<span>2000грн</span>
								</div>
							</div>
						</div>
					</div>
					<div class="summary">
						<span>Итого: {{ count*2000 }}грн</span>
					</div>
				</div>
				<div class="cart_footer">
					<div class="container-fluid">
						<div class="row">
							<div class="col">
								<div class="look_more" @click="cartShow = !cartShow">
									<span>выбрать еще что-нибудь</span><i class="fas fa-angle-left"></i>
								</div>
							</div>
							<div class="col">
								<div class="buy"><a href="#">оформить заказ</a></div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		props: {
			cartShow: {
				type: String,
				default () {
					return
				}
			}
		},
		data () {
			return {
				count: 1
			}
		},
		methods: {
			increase () {
				this.count += 1
			},
			decrease () {
				if ( this.count <= 0) {
					this.count = 1
				}
				else {
					this.count -= 1
				}
			}
		},
		computed: {
			myCount () {
				return {
					count: this.count
				}
			}
		}
	}
</script>	



