import Vue from 'vue'
import VueRouter from 'vue-router'
import app from './App'
import hello from './components/pages/Home-page'
import Creator from './components/pages/Creator-Page'
import HowWork from './components/pages/How-itWork'
import CategoryPage from './components/pages/Category-page'
import ProductPage from './components/pages/Product-page'
import CompanyPage from './components/pages/Aboute-company'

import './assets/style.sass'
import '../node_modules/bootstrap/scss/bootstrap.scss'
import VueCarousel from 'vue-carousel'
Vue.use(VueRouter)
Vue.use(VueCarousel)

const router = new VueRouter({
  mode: 'history',
  routes: [{
		path: '/',
		component: hello
	}, {
		path: '/creator',
		component: Creator
  }, {
		path: '/how-it-work',
		component: HowWork
	}, {
    path: '*',
    redirect: '/'
  }, {
		path: '/category-page',
		component: CategoryPage
	}, {
		path: '/product-page',
		component: ProductPage
	}, {
		path: '/aboute-comp',
		component: CompanyPage
	}]
})
new Vue({
  el: '#app',
  router,
	render: h => h(app)
})



