<template>
	<aside>
		<section class="category-tree">
			<div class="title">
				<h3>По категориям:</h3>
			</div>
			<ul class="cat_items">
				<li class="item"
					v-for="(item, itemId) in dropmenu"
					:key="itemId"
				>
				<a :href="item.link">{{item.title}}</a>
				</li>
			</ul>
		</section>
		<section class="price-choose">
			<div class="title">
				<h3>По цене цену:</h3>
			</div>
			<vue-slider v-model="value"></vue-slider>
			<div class="button">подтвердить</div>
		</section>
		<section class="color-picker">
			<div class="title">
				<h3>По цвету цвет:</h3>
			</div>
			<div class="colors">
				<ul>
					<li class="items">черный</li>
					<li class="items">красный</li>
					<li class="items">синий</li>
					<li class="items">фиолетовый</li>
					<li class="items">зеленый</li>
				</ul>
			</div>
		</section>
	</aside>
</template>

<script>
import vueSlider from 'vue-slider-component'
	export default {
		components: {
			vueSlider
		},
		props: ['dropmenu'],
		data () {
			return {
				dropmenu: this.dropmenu,
				value: [ 0, 4590 ]
			}
		}
	}
</script>



